



































































































































































































































































.template {
  background-color: #fff;
  color: rgb(17, 17, 17);
  width: 100%;
  min-height: 29.6cm;
  word-break: break-all;
  position: relative;
}

.padding {
  padding: 40px;
}

.logo {
  width: 70%;
}

.header {
  margin-bottom: 40px;
  p {
    font-size: 0.875rem;
    margin-bottom: 0;
    &.small {
      margin-bottom: 0;
    }
  }
}

.title {
  width: 160px;
  text-align: right;
  font-size: 12px;
  margin-right: 17px;
  flex-shrink: 0;
}

.subtitle {
  width: 150px;
  text-align: right;
  font-size: 11px;
  margin-right: 26px;
  flex-shrink: 0;
}

h2 {
  font-size: 15px;
}

h4 {
  font-size: 0.9rem;
}

.personal-img {
  position: relative;
  right: -9px;
  width: 95px;
  height: 110px;
  margin-left: 55px;
  margin-right: 27px;
  background-color: gray;
  object-fit: cover;
}

.small-icon {
  width: 14px;
  height: 14px;
  margin-right: 10px;
  &.pin {
    margin-left: 3px;
    width: 10px;
  }
  &.mail {
    width: 14px;
    height: 11px;
  }
}

.personal-info-with-icon {
  font-size: 11px;
  margin-bottom: 0.6rem;
}

.work-info {
  &__info {
    text-align: left;
    font-size: 10px;
    margin-bottom: 0.4rem;
  }
  &__text {
    text-align: left;
    font-size: 10px;
    .dot {
      position: relative;
      top: -0.1rem;
      display: inline-block;
      width: 2px;
      height: 2px;
      background-color: black;
    }
  }
}

.language {
  p {
    text-align: center;
    font-size: 12px;
    margin-bottom: 0;
    &.small {
      font-size: 9px;
    }
  }
}

.blue-line {
  position: relative;
  width: 100%;
  background-color: #1493CB;
  height: 1px;
  &::after {
    content: '';
    display: block;
    width: 10px;
    height: 10px;
    background-color: #1493CB;
    position: absolute;
    bottom: 0;
    right: 0;
  }
}

.dark-blue {
  color: #0E4194;
}

.light-blue {
  color: #1493CB;
}

